.TrItem {
  -webkit-transition: all 280ms ease-out;
  -o-transition: all 280ms ease-out;
  transition: all 280ms ease-out;
}

.TrItem:hover:nth-child(2n) {
  cursor: pointer;
  background-color: #f0ffff;
}

.TrItem:hover:nth-child(2n + 1) {
  cursor: pointer;
  background-color: #d0d0d0;
  -webkit-transition: all 280ms ease-out;
  -o-transition: all 280ms ease-out;
  transition: all 280ms ease-out;
}



