.Prodoct_Item {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 250px;
  -webkit-box-shadow: 0px 0px 12px 0px rgba(4, 8, 7, 0.59);
          box-shadow: 0px 0px 12px 0px rgba(4, 8, 7, 0.59);
  margin: 5px;
}

.Prodoct_Item span {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.Prodoct_Item .Product_Image {
  margin: 0 auto;
  height: 250px;
  width: 200px;
}
.Prodoct_Item .CompareContainer {
  position: relative;
  height: 70px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.Prodoct_Item .CompareContainer .CompareItem {
  height: 50px;
  width: 50px;
  -webkit-transition: all 300ms ease-out;
  -o-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
  position: absolute;
}

.Prodoct_Item .CompareContainer .CompareItem:hover {
  cursor: pointer;
  height: 60px;
  width: 60px;
}



